.videoCard{
    width: 270px;
    margin-bottom: 40px;
}
.videoCard:hover{
    cursor: pointer;
}
.videoCard__thumbnail{
    height: 140px;
    width: 250px;
}
.videoCard__info{
    display: flex;
    padding-right: 30px;
    margin-top: 10px;
}
.videoCard__text{
    margin-left: 15px;
}
.videoCard__text > h4{
    font-size: 14px;
    margin-bottom: 5px;
}
.videoCard__text > p{
    font-size:14px ;
    color: gray;
}
.video__avatar{
    height: 30px !important;
    width: 30px !important;
}